var Pillbox = React.createClass({
    getInitialState: function(){
        return { items: [], selectedItems: [], value: "" };
    },
    handleInput: function(){
        var filterText = this.refs.input.value;
        this.search(filterText);
        this.setState({ value: filterText});
    },
    handleDropdown: function(){
        this.search('');
    },
    handleAddItem: function(){
        this.state.selectedItems.push();
        this.setState({ selectedItems: this.state.selectedItems});
    },
    search: function(filterText) {
        
        mockAjax(this.props.src, { filter: filterText }).then(
            function(data){
                this.setState({ items: data});
            }.bind(this)
        )
        
        // $.get(this.props.src, { filter: filterText }, function(data){            
        //     this.setState({ items: data});
        // }, "json");
    },
    render: function(){
        
        
        return  <div className="pillbox">
                    <input ref="input" type="text" onChange={this.handleInput} value={this.state.value} /><span onClick={this.handlerDropdown}>^</span>
                    <ul ref="results">
                        {this.state.items.map(function(item){
                            return <li onClick={this.handleAddItem.bind(this, item)}>{item}</li> 
                        })}
                    </ul>
                    <div className="container">
                        {this.state.selectedItems.map(function(item){
                            return <div>{item}</div>;
                        }.bind(this))}
                    </div>
                </div>
    }    
});

module.exports = Pillbox;


